import React from 'react';
import styled from 'styled-components';
import { PortfolioFlexContainer } from '../Global/FlexContainer';
import { PrimaryButton } from '../Global/Button';

export const Project = function (props) {
  const { theme, portfolioColor, buttonColor, project } = props;
  return (
    <PortfolioFlexContainer
      className="project"
      portfolioColor={portfolioColor}
      height="100%"
      width="101vw"
      flow="column nowrap"
      justify="flex-start"
      align="center"
      textColor={buttonColor}
      overFlowY="auto"
      ZIndex={5}
      project={true}>
      <h2 className="project-title">{project.title}</h2>
      <h4 className="project-brief">{project.brief}</h4>
      <ul className="my-involvement">
        {project.myInvolvement &&
          project.myInvolvement.map((item, idx) => {
            return (
              <li className="my-involvement__item" key={idx}>
                <p>{item}</p>
              </li>
            );
          })}
      </ul>
      <section className="project-image-container">
        <img src={project.coverPhoto} alt={project.photoAltText} />
      </section>
      <p className="project-description">{project.description}</p>
      <h4 className="technologies-used-title">Technologies Used</h4>
      <p className="technologies-used">{project.technologies}</p>
      <section className="button-container">
        <a href={project.code}>
          <PrimaryButton borderColor={buttonColor} textColor={buttonColor} textHoverColor={portfolioColor} portfolioColor={portfolioColor}>
            View Code
          </PrimaryButton>
        </a>
        <a href={project.link}>
          <PrimaryButton borderColor={buttonColor} textColor={buttonColor} textHoverColor={portfolioColor} portfolioColor={portfolioColor}>
            View Live Site
          </PrimaryButton>
        </a>
      </section>
    </PortfolioFlexContainer>
  );
};
