import React from 'react';
import styled from 'styled-components';
import { DateTime } from 'luxon';

const StyledFooter = styled.footer`
  position: absolute;
  height: 5rem;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 0;
  font-family: ${(props) => props.theme.text.secondary};
  font-size: 1.6rem;
  color: ${(props) => props.theme.colors.grayScale.offWhite};

  & .logo {
    height: 3rem;
    width: auto;
  }

  & .logo-text {
    font-family: Purely Me;
    margin: 0 2rem;
    ${(props) => (props.timeofday === 'day' ? `color: ${props.theme.colors.grayScale.raisinBlack}` : null)}
  }

  & .copyright {
    ${(props) => (props.timeofday === 'day' ? `color: ${props.theme.colors.grayScale.raisinBlack}` : null)}
  }

  @media ${(props) => props.theme.screens.largeMobile.landscape} {
    font-size: 1.2rem;

    & .logo {
      height: 2rem;
    }
  }

  @media ${(props) => props.theme.screens.mediumMobile.portrait} {
    height: 3.5rem;
    font-size: 1.2rem;

    & .logo {
      height: 2rem;
    }
  }
`;

export const Footer = (props) => {
  const { theme, location } = props;
  return (
    <StyledFooter timeofday={location === 'Contact' || location === 'Hire' ? 'day' : 'night'}>
      <img className="logo" src={location === 'Contact' || location === 'Hire' ? theme.logo.imageDay : theme.logo.imageNight} alt="PureNSpiration Logo" />
      <p className="logo-text">{theme.logo.text}</p>
      <p className="copyright">&copy; {DateTime.now().year}</p>
    </StyledFooter>
  );
};
