import React, { useEffect, useRef } from 'react';
import { Link, useParams } from 'react-router-dom';
import styled from 'styled-components';
import Typed from 'typed.js';
import { PrimaryButton } from '../Global/Button';
import { HomeFlexContainer, StyledFlexContainer } from '../Global/FlexContainer';
import { Navigation } from '../Global/Navigation';
import { HomeTyped } from '../Global/TypedContainers/HomeTyped';

const StyledHome = styled.section`
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-flow: ${(props) => props.flow};
  justify-content: ${(props) => props.justify};
  align-items: ${(props) => props.align};
  background-color: ${(props) => props.homeColor};
`;

export const Home = function (props) {
  const { theme, homeColor, buttonColor, setButtonColor, location, setLocation } = props;

  // Create reference to store the DOM element containing the animation
  const el = useRef(null);
  // Create reference to store the Typed instance itself
  const typed = useRef(null);

  useEffect(() => {
    const options = {
      strings: [
        'Since forever, I have always been someone who has been fascinated by data, technology, and being creative.  With that being said, I have dabbled in graphic design and typography in my spare time for a while now.  Late in the year 2016, I fell in love with web development as the perfect medium for everything I love.  As a bonus, being a `Highly Sensitive Person` as Dr. Elaine N Aron puts it, helps me to make sure no details get lost in translation to the best user experience possible.',
      ],
      typeSpeed: 40,
      backSpeed: 40,
    };

    // elRef refers to the <span> rendered below
    typed.current = new Typed(el.current, options);

    return () => {
      // Make sure to destroy Typed instance during cleanup
      // to prevent memory leaks
      typed.current.destroy();
    };
  }, []);

  return (
    <StyledHome className="column_1-24 row_1-18" homeColor={homeColor}>
      <StyledFlexContainer height="100%" width="100vw" flow="row wrap" justify="center" align="center">
        <HomeFlexContainer height="max-content" width="75%" flow="row nowrap" justify="space-between" align="center">
          <img src="/DIST/CSS/Images/NCluff_Cartoon.svg" alt="Cartoon Me" />
          <HomeTyped>
            <header className="introduction">
              Hello there! My name is <span>Nathan Cluff.</span> I am a full-stack web developer currently based in Utah.
            </header>
            <section className="typing">
              <span ref={el}></span>
            </section>
            <section className="button-container">
              <Link to="Contact" onClick={() => setLocation('Contact')}>
                <PrimaryButton height="5rem" width="20rem" textColor={buttonColor} borderColor={buttonColor} textHoverColor={homeColor}>
                  <i className="fas fa-mobile-screen" />
                  Contact Me
                </PrimaryButton>
              </Link>

              <Link to="Hire" onClick={() => setLocation('Hire')}>
                <PrimaryButton height="5rem" width="20rem" textColor={buttonColor} borderColor={buttonColor} textHoverColor={homeColor}>
                  <i className="fas fa-briefcase" />
                  Hire Me
                </PrimaryButton>
              </Link>
            </section>
          </HomeTyped>
        </HomeFlexContainer>
      </StyledFlexContainer>
    </StyledHome>
  );
};
