import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import axios from 'axios';
import qs from 'qs';
import { produce } from 'immer';
import { ContactFlexContainer } from '../Global/FlexContainer';
import { StyledHeader } from '../Global/Header';
import ErrorContainer from '../Global/Errors';
import { Form } from '../Global/Form';
import { FormSection } from '../Global/FormSection';
import { FormInput, FormTextArea } from '../Global/Input';
import { FormLabel } from '../Global/Label';
import { PrimaryButton } from '../Global/Button';
// import { Calendar } from '../../../../Penciled/Public/Calendar';

const initialMessage = {
  firstname: '',
  lastname: '',
  email: '',
  subject: '',
  message: '',
};

export const Contact = function (props) {
  const { theme, contactColor, buttonColor, textColor, location, errors, setErrors } = props;

  const [message, setMessage] = useState(initialMessage);

  const handleMessageChange = (e) => {
    e.preventDefault();
    const { name, value } = e.target;

    const updated = produce(message, (roughDraft) => {
      roughDraft[name] = value;
    });
    setMessage(updated);
  };

  return (
    <ContactFlexContainer
      theme={theme}
      contactColor={contactColor}
      className="column_1-24 row_1-18"
      height="100%"
      width="100%"
      flow="column nowrap"
      justify="space-evenly"
      align="center">
      <StyledHeader className="header" timeofday={location === 'Contact' ? 'day' : 'night'} textSize="8rem" textWeight="bold" textAlign="center">
        {`Contact`.toUpperCase()}
      </StyledHeader>
      <ErrorContainer className="contact-errors">
        {errors && (
          <ol>
            {Object.keys(errors).map((error, index) => {
              if (errors[error] !== '') {
                return (
                  <li className="error" key={index}>
                    {errors[error]}
                  </li>
                );
              }
            })}
          </ol>
        )}
      </ErrorContainer>
      <Form position="relative" formHeight="100vh" formWidth="75%" formPadding="3rem" flow="column nowrap" justify="flex-start" align="center">
        <FormSection sectionWidth="100%" flow="row nowrap" justify="space-evenly" align="center" className="names">
          <FormSection minHeight="10rem" sectionWidth="50%" flow="column-reverse nowrap" justify="space-evenly" align="center">
            <FormInput
              type={'text'}
              inputHeight="80%"
              inputWidth="90%"
              placeholder="First Name"
              padding="2rem"
              focusTransform="translate(-2rem, -225%) scale(.8)"
              id="firstname"
              name="firstname"
              onChange={(e) => handleMessageChange(e)}
            />
            <FormLabel leftIndent="6rem" htmlFor="firstname" elementTop="50%">
              {`First Name`.toUpperCase()}
            </FormLabel>
          </FormSection>
          <FormSection minHeight="10rem" sectionWidth="50%" flow="column-reverse nowrap" justify="space-evenly" align="center">
            <FormInput
              type={'text'}
              inputHeight="80%"
              inputWidth="90%"
              placeholder="Last Name"
              padding="2rem"
              focusTransform="translate(-2rem, -225%) scale(.8)"
              id="lastname"
              name="lastname"
              onChange={(e) => handleMessageChange(e)}
            />
            <FormLabel leftIndent="6rem" htmlFor="lastname" elementTop="50%">
              {`Last Name`.toUpperCase()}
            </FormLabel>
          </FormSection>
        </FormSection>
        <FormSection minHeight="10rem" sectionWidth="100%" flow="column-reverse nowrap" justify="space-evenly" align="center">
          <FormInput
            type={'text'}
            inputHeight="80%"
            inputWidth="95%"
            placeholder="Email"
            padding="2rem"
            focusTransform="translate(-2rem, -225%) scale(.8)"
            id="email"
            name="email"
            onChange={(e) => handleMessageChange(e)}
          />
          <FormLabel leftIndent="6rem" htmlFor="email" elementTop="50%">
            {`Email`.toUpperCase()}
          </FormLabel>
        </FormSection>
        <FormSection minHeight="10rem" sectionWidth="100%" flow="column-reverse nowrap" justify="space-evenly" align="center">
          <FormInput
            type={'text'}
            inputHeight="80%"
            inputWidth="95%"
            placeholder="Subject"
            padding="2rem"
            focusTransform="translate(-2rem, -225%) scale(.8)"
            id="subject"
            name="subject"
            onChange={(e) => handleMessageChange(e)}
          />
          <FormLabel leftIndent="6rem" htmlFor="subject" elementTop="50%">
            {`Subject`.toUpperCase()}
          </FormLabel>
        </FormSection>
        <FormSection
          className="messaging"
          minHeight="32rem"
          sectionWidth="100%"
          flow="column-reverse nowrap"
          justify="space-evenly"
          align="center"
          textArea={true}
          margin="1rem 0 4rem">
          <FormTextArea
            inputHeight="30rem"
            minHeight="30rem"
            inputWidth="95%"
            id="message"
            name="message"
            minLength={0}
            maxLength={2000}
            focusTransform="translate(-2rem, -225%) scale(.8)"
            padding="2rem"
            placeholder="Your message here..."
            onChange={(e) => handleMessageChange(e)}
          />
          <FormLabel leftIndent="6rem" htmlFor="message" elementTop="15%" textArea={true}>
            {`Message`.toUpperCase()}
          </FormLabel>
          <FormLabel className="characters" characterCount={true} textArea={false} elementBottom="1rem" elementRight="26rem">
            Characters Left: {2000 - message.message.length} Characters
          </FormLabel>
          <PrimaryButton
            className="send-message"
            borderColor={buttonColor}
            textColor={buttonColor}
            textHoverColor={textColor}
            contactColor={contactColor}
            sendMessage={true}
            onClick={(e) => {
              e.preventDefault();
              try {
                const sendMessage = async () => {
                  const response = await axios({
                    method: 'POST',
                    url: '/Contact',
                    data: qs.stringify(message),
                  });
                  console.log(response);
                };
                sendMessage();
              } catch (error) {
                if (error.message) {
                  setErrors({ ...errors, ['error']: error.message });
                } else {
                  setErrors({ ...errors, ['error']: '' });
                }
                console.error(error);
              }
            }}>
            Send Message
          </PrimaryButton>
        </FormSection>
      </Form>
      {/* <Calendar theme={theme} /> */}
    </ContactFlexContainer>
  );
};
