import React, { useState } from 'react';
import { Routes, Route, Link } from 'react-router-dom';
import styled from 'styled-components';
import { PortfolioFlexContainer } from '../Global/FlexContainer';
import { PrimaryButton } from '../Global/Button';
import { Project } from './Project';

export const Portfolio = function (props) {
  const { theme, portfolioColor, buttonColor, data } = props;
  const [project, setProject] = useState('');

  return (
    <PortfolioFlexContainer
      className="column_1-24 row_1-18"
      portfolioColor={portfolioColor}
      height="100%"
      width="100vw"
      flow="column nowrap"
      justify="flex-start"
      align="center"
      overFlowY="auto">
      <Routes>
        <Route
          path=":project"
          element={project && project.title && <Project theme={theme} portfolioColor={portfolioColor} buttonColor={buttonColor} project={project} />}></Route>
      </Routes>
      {data &&
        data.projects.map((project, index) => {
          return (
            <PortfolioFlexContainer
              height="80vh"
              minHeight="80vh"
              width="90%"
              flow="row wrap"
              justify="space-evenly"
              align="flex-start"
              margin="10rem 0"
              overFlowY="auto"
              className="individual-project"
              key={index}
              textColor={buttonColor}>
              <section className="cover-image-container">
                <img src={project.coverPhoto} alt={project.photoAltText} className="cover-image-container__image" />
              </section>
              <section className="project-info-container">
                <h2 className="project-title">{project.title}</h2>
                <h4 className="project-brief">{project.brief}</h4>
                <ul className="my-involvement">
                  {project.myInvolvement &&
                    project.myInvolvement.map((item, idx) => {
                      return (
                        <li className="my-involvement__item" key={idx}>
                          <p>{item}</p>
                        </li>
                      );
                    })}
                </ul>
                <Link to={project.title.split(' ').join('-')}>
                  <PrimaryButton
                    borderColor={buttonColor}
                    textColor={buttonColor}
                    textHoverColor={portfolioColor}
                    portfolioColor={portfolioColor}
                    onClick={(e) => {
                      setProject(project);
                    }}>
                    View Details
                  </PrimaryButton>
                </Link>
              </section>
            </PortfolioFlexContainer>
          );
        })}
    </PortfolioFlexContainer>
  );
};
