import React, { useState, useEffect } from 'react';
import { Routes, Route, useParams } from 'react-router-dom';
import axios from 'axios';
import { Navigation } from '../Global/Navigation';
import { Home } from './Home';
import { Footer } from '../Global/Footer';
import { About } from './About';
import { Resume } from './Resume';
import { Portfolio } from './Portfolio';
import { Contact } from './Contact';
import { Hire } from './Hire';

export default function Application(props) {
  const { theme } = props;
  const [data, setData] = useState('');
  const [navigationOpen, setNavigationOpen] = useState(false);
  let [location, setLocation] = useState(useParams()['*']);
  const [errors, setErrors] = useState([]);
  const [buttonColor, setButtonColor] = useState('');

  useEffect(() => {
    const getData = async () => {
      try {
        const response = await axios({
          method: 'GET',
          url: '/data',
        });
        setData(response.data.data);
      } catch (error) {
        console.error(error);
      }
    };
    getData();
  }, []);

  useEffect(() => {
    location === ''
      ? setButtonColor(theme.colors.primaryColor)
      : location === 'About'
      ? setButtonColor(theme.colors.grayScale.offWhite)
      : location === 'Portfolio' || location.split('/')[0] === 'Portfolio'
      ? setButtonColor(theme.colors.grayScale.offWhite)
      : location === 'Resume'
      ? setButtonColor(theme.colors.grayScale.offWhite)
      : location === 'Contact'
      ? setButtonColor(theme.colors.primaryColor)
      : location === 'Hire'
      ? setButtonColor(theme.colors.grayScale.raisinBlack)
      : setButtonColor(theme.colors.primaryColor);
  }, [location]);

  return (
    <section className="master-grid-container">
      <Navigation
        theme={theme}
        content={{
          location: useParams()['*'].toUpperCase() || 'HOME',
          navBackground:
            useParams()['*'].toUpperCase() === ''
              ? theme.colors.primaryColor
              : useParams()['*'].toUpperCase() === 'ABOUT'
              ? theme.colors.secondaryColor
              : useParams()['*'].toUpperCase() === 'PORTFOLIO' || useParams()['*'].toUpperCase().split('/')[0] === 'PORTFOLIO'
              ? theme.colors.quinaryColor
              : useParams()['*'].toUpperCase() === 'RESUME'
              ? theme.colors.quaternaryColor
              : useParams()['*'].toUpperCase() === 'CONTACT'
              ? theme.colors.primaryColor
              : useParams()['*'].toUpperCase() === 'HIRE'
              ? theme.colors.tertiaryColor
              : theme.colors.primaryColor,
          background:
            useParams()['*'].toUpperCase() === ''
              ? theme.colors.grayScale.raisinBlack
              : useParams()['*'].toUpperCase() === 'ABOUT'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'PORTFOLIO' || useParams()['*'].toUpperCase().split('/')[0] === 'PORTFOLIO'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'RESUME'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'CONTACT'
              ? theme.colors.grayScale.raisinBlack
              : useParams()['*'].toUpperCase() === 'HIRE'
              ? theme.colors.grayScale.raisinBlack
              : theme.colors.grayScale.raisinBlack,
          infoTextColor:
            useParams()['*'].toUpperCase() === ''
              ? theme.colors.primaryColor
              : useParams()['*'].toUpperCase() === 'ABOUT'
              ? theme.colors.secondaryColor
              : useParams()['*'].toUpperCase() === 'PORTFOLIO' || useParams()['*'].toUpperCase().split('/')[0] === 'PORTFOLIO'
              ? theme.colors.quinaryColor
              : useParams()['*'].toUpperCase() === 'RESUME'
              ? theme.colors.quaternaryColor
              : useParams()['*'].toUpperCase() === 'CONTACT'
              ? theme.colors.primaryColor
              : useParams()['*'].toUpperCase() === 'HIRE'
              ? theme.colors.tertiaryColor
              : theme.colors.primaryColor,
          infoBorderColor:
            useParams()['*'].toUpperCase() === ''
              ? theme.colors.primaryColor
              : useParams()['*'].toUpperCase() === 'ABOUT'
              ? theme.colors.secondaryColor
              : useParams()['*'].toUpperCase() === 'PORTFOLIO' || useParams()['*'].toUpperCase().split('/')[0] === 'PORTFOLIO'
              ? theme.colors.quinaryColor
              : useParams()['*'].toUpperCase() === 'RESUME'
              ? theme.colors.quaternaryColor
              : useParams()['*'].toUpperCase() === 'CONTACT'
              ? theme.colors.primaryColor
              : useParams()['*'].toUpperCase() === 'HIRE'
              ? theme.colors.tertiaryColor
              : theme.colors.primaryColor,
          linkColor:
            useParams()['*'].toUpperCase() === ''
              ? theme.colors.grayScale.raisinBlack
              : useParams()['*'].toUpperCase() === 'ABOUT'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'PORTFOLIO' || useParams()['*'].toUpperCase().split('/')[0] === 'PORTFOLIO'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'RESUME'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'CONTACT'
              ? theme.colors.grayScale.raisinBlack
              : useParams()['*'].toUpperCase() === 'HIRE'
              ? theme.colors.grayScale.raisinBlack
              : theme.colors.grayScale.raisinBlack,
          linkActiveColor:
            useParams()['*'].toUpperCase() === ''
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'ABOUT'
              ? theme.colors.grayScale.raisinBlack
              : useParams()['*'].toUpperCase() === 'PORTFOLIO' || useParams()['*'].toUpperCase().split('/')[0] === 'PORTFOLIO'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'RESUME'
              ? theme.colors.grayScale.offWhite
              : useParams()['*'].toUpperCase() === 'CONTACT'
              ? theme.colors.grayScale.raisinBlack
              : useParams()['*'].toUpperCase() === 'HIRE'
              ? theme.colors.grayScale.raisinBlack
              : theme.colors.grayScale.offWhite,
        }}
        navigationOpen={navigationOpen}
        setNavigationOpen={setNavigationOpen}
      />
      <Routes>
        <Route
          path="/*"
          element={
            <Home
              theme={theme}
              navigationOpen={navigationOpen}
              setNavigationOpen={setNavigationOpen}
              homeColor={theme.colors.grayScale.raisinBlack}
              buttonColor={buttonColor}
              setButtonColor={setButtonColor}
              location={location}
              setLocation={setLocation}
            />
          }
        />
        <Route
          path="/About/*"
          element={
            <About
              theme={theme}
              aboutColor={theme.colors.secondaryColor}
              buttonColor={buttonColor}
              setButtonColor={setButtonColor}
              location={location}
              setLocation={setLocation}
            />
          }></Route>
        <Route
          path="/Portfolio/*"
          element={
            <Portfolio theme={theme} portfolioColor={theme.colors.quinaryColor} buttonColor={buttonColor} setButtonColor={setButtonColor} data={data} />
          }></Route>
        <Route
          path="/Resume/*"
          element={
            <Resume theme={theme} resumeColor={theme.colors.quaternaryColor} buttonColor={buttonColor} setButtonColor={setButtonColor} data={data} />
          }></Route>
        <Route
          path="/Contact/*"
          element={
            <Contact
              theme={theme}
              contactColor={theme.colors.primaryColor}
              location={location}
              buttonColor={buttonColor}
              setButtonColor={setButtonColor}
              textColor={theme.colors.grayScale.raisinBlack}
              data={data}
              errors={errors}
              setErrors={setErrors}
            />
          }></Route>
        <Route
          path="/Hire/*"
          element={
            <Hire
              theme={theme}
              hireColor={theme.colors.tertiaryColor}
              location={location}
              buttonColor={buttonColor}
              setButtonColor={setButtonColor}
              textColor={theme.colors.grayScale.raisinBlack}
              data={data}
              errors={errors}
              setErrors={setErrors}
            />
          }></Route>
      </Routes>
      <Footer theme={theme} location={location} />
    </section>
  );
}
