import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { DateTime } from 'luxon';
import { HireFlexContainer } from '../Global/FlexContainer';
import { StyledHeader } from '../Global/Header';
import { ScheduleContainer } from '../Global/ScheduleContainer';
import { Schedule } from 'scheduleit';

export const Hire = function (props) {
  const { theme, hireColor, buttonColor, textColor, location, errors, setErrors } = props;
  const [schedule, setSchedule] = useState('');

  useEffect(() => {
    const schedule = new Schedule({
      theme: {
        message: 'Ready For Work!',
        timeOfDay: {
          set: 'Auto',
        },
        font: theme.text.secondary,
        colors: {
          primary: theme.colors.tertiaryColor,
          secondary: theme.colors.secondaryColor,
          tertiary: theme.colors.quaternaryColor,
        },
      },
      details: {
        firstname: 'Nathan',
        lastname: 'Cluff',
        company: 'PureNSpiration',
        email: 'ncluff003@purenspiration.com',
      },
      schedule: {
        overnight: false,
        start: 9,
        end: 17,
        appointmentBuffer: 15,
        maximumAppointmentLength: 180,
      },
    });
    setSchedule(schedule);
  }, []);

  return (
    <HireFlexContainer
      theme={theme}
      hireColor={hireColor}
      className="column_1-24 row_1-18"
      height="100%"
      width="100%"
      flow="column nowrap"
      justify="space-evenly"
      align="center">
      <StyledHeader location={location} timeofday={location === 'Contact' ? 'day' : 'night'} textSize="8rem" textWeight="bold" textAlign="center">
        {`Hire Me`.toUpperCase()}
      </StyledHeader>
      <ScheduleContainer id="schedule"></ScheduleContainer>
    </HireFlexContainer>
  );
};
