import styled from 'styled-components';

export const FormLabel = styled.label`
  position: absolute;
  top: ${(props) => props.elementTop};
  left: ${(props) => props.leftIndent};
  display: flex;
  padding: ${(props) => props.padding};
  margin: ${(props) => props.margin};
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  background-color: transparent;
  font-family: ${(props) => props.theme.text.secondary};
  color: ${(props) => `${props.theme.colors.grayScale.offWhite}cc`};
  transition: transform 0.5s, background-color 0.25s, border 0.25s, color 0.25s;
  transform: translate(0, -50%);
  font-size: 2.4rem;
  z-index: 5;

  &:hover {
    cursor: ${(props) => props.hoverCursor};
  }

  ${(props) => (props.textArea === true ? `top: ${props.elementTop}` : null)};
  ${(props) => (props.characterCount === true ? `bottom: ${props.elementBottom}` : null)};
  ${(props) => (props.characterCount === true ? `right: ${props.elementRight}` : null)};
  ${(props) => (props.characterCount === true ? `font-size: 2rem` : null)};
`;
