import React from 'react';
import styled from 'styled-components';

export const ButtonContainer = styled.section`
  position: relative;
  height: ${(props) => props.containerHeight};
  width: ${(props) => props.containerWidth};
  display: flex;
  flex-flow: ${(props) => props.flow};
  justify-content: ${(props) => props.justify};
  align-items: ${(props) => props.align};
`;

export const StyledButton = styled.button`
  position: relative;
  height: ${(props) => props.height};
  width: ${(props) => props.width};
  min-height: ${(props) => props.minHeight};
  min-width: ${(props) => props.minWidth};
  border-radius: ${(props) => props.borderRadius};
  justify-self: ${(props) => props.selfJustify};
  align-self: ${(props) => props.selfAlign};

  &:hover {
    cursor: pointer;
  }
`;

export const NavigationButton = styled(StyledButton)`
  position: absolute;
  bottom: 17rem;
  right: 1.5rem;
  border: none;
  transform: translate(0rem, 14rem);
  background-color: ${(props) => props.theme.colors.grayScale.raisinBlack};
  &:before,
  &:after {
    position: absolute;
    content: '';
    border: none;
    height: ${(props) => props.pseudoHeight};
    width: ${(props) => props.pseudoWidth};
    min-height: ${(props) => props.minHeight};
    min-width: ${(props) => props.minWidth};
    border-radius: ${(props) => props.borderRadius};
    background-color: ${(props) => props.theme.colors.grayScale.raisinBlack};
  }
  &:before {
    transform: translate(-2rem, -1.5rem);
  }
  &:after {
    transform: translate(-2rem, 1.1rem);
  }
`;

export const PrimaryButton = styled(StyledButton)`
  height: 5rem;
  width: 20rem;
  border: 0.2rem solid ${(props) => `${props.borderColor}cc`};
  border-radius: 0.9rem;
  background-color: transparent;
  font-family: ${(props) => props.theme.text.secondary};
  font-size: 1.6rem;
  color: ${(props) => `${props.textColor}cc`};
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
  margin: ${(props) => props.margin};

  &:hover {
    color: ${(props) => `${props.textHoverColor}`};
    background-color: ${(props) => `${props.borderColor}`};
    border-color: ${(props) => `${props.borderColor}`};
    transition: background-color 0.5s, border 0.5s, color 0.5s;
  }
  ${(props) => (props.sendMessage === true ? `position: absolute` : null)};
  ${(props) => (props.sendMessage === true ? `bottom: 1.5rem` : null)};
  ${(props) => (props.sendMessage === true ? `right: 4.5rem` : null)};

  ${(props) => (props.size === 'small' ? `height: 4rem` : null)};
  ${(props) => (props.size === 'small' ? `width: 10rem` : null)};
`;
